.ant-layout {
  height: 100%;
}

.header {
  padding: 0;
}

.home-logo {
  width: 60px;
  height: 60px;
  background-color: rgb(231, 227, 227);
  position: absolute;
  left: 5px;
  top: 2.5px;
  margin: 0 auto 20px;
  box-shadow: 0 0 50px rgb(0 0 0 / 10%);
  opacity: 0.8;
}

.logo {
  width: 200px;
  height: 70px;
  background: url('~@/assets/logo2.png') no-repeat center / 90px auto;
}

.layout-content {
  overflow-y: auto;
  background: center/cover url('~@/assets/login.png');
}

.user-info {
  position: absolute;
  right: 0;
  top: 0;
  padding-right: 20px;
  color: #fff;

  .user-name {
    font-size: 16px;
    font-weight: bold;
    color: rgb(251, 209, 43);
    margin-right: 20px;
  }

  .user-menu {
    font-size: 16px;
    font-weight: bold;
    color: yellow;
    margin-right: 20px;
  }

  .user-logout {
    font-size: 16px;
    font-weight: bold;
    color: red;
    width: 75px;
    //white-space: nowrap; /* 不斷行 */
    display: inline-block;
    cursor: pointer;
  }
}
.ant-layout-header {
  //background-color: rgb(76, 168, 201) !important;
  padding: 0 !important;
}
